    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .content__title {
    font-size: 16px;
    line-height: 20px;
  }
}

.content__text {
  font-weight: 300;
  font-size: 18px;
  line-height: 22px;
}
@media (max-width: 1023px) {
  .content__text {
    font-size: 16px;
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .content__text {
    font-size: 14px;
    line-height: 17px;
  }
}

.content__text p {
  margin: 0 0 10px;
  padding: 0;
}

.content__icon {
  flex-shrink: 0;
  margin-right: 20px;
}
.content__icon--hidden {
  display: inline-flex;
}
@media (max-width: 1023px) {
  .content__icon {
    width: 90px;
    height: 105px;
  }
}
@media (max-width: 767px) {
  .content__icon {
    width: 90px;
    height: 105px;
  }
  .content__icon--hidden {
    display: none;
  }
}

.article__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.article__item {
  position: relative;
  display: block;
  margin-bottom: 8px;
  margin-left: 20px;
}
.article__item::before {
  content: "";
  position: absolute;
  top: 5px;
  left: -20px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #2a9c9e;
}
@media (max-width: 767px) {
  .article__item::before {
    width: 10px;
    height: 10px;
  }
}

.article__link {
  display: block;
  margin-top: 20px;
  margin-bottom: 10px;
  padding: 15px 0 15px 15px;
  font-style: italic;
  font-weight: 700;
  border-left: 5px solid #2a9c9e;
}

.article__link a {
  font-weight: 700;
  font-style: italic;
  color: #2a9c9e;
}

.footer {
  position: relative;
  z-index: 2;
  background-color: #ffffff;
  box-shadow: 0 -4px 16px rgba(77, 91, 163, 0.1);
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  min-height: 100px;
  padding-top: 23px;
  padding-bottom: 23px;
}
@media (max-width: 1023px) {
  .footer__wrapper {
    padding-top: 23px;
    padding-bottom: 23px;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    min-height: 69px;
    padding-top: 16px;
    padding-bottom: 16px;
  }
}

.social__list {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

@media (hover: hover) {
  .social__item:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.social__item.focus-visible:focus {
  opacity: 0.6;
}
.social__item.focus-visible:active {
  opacity: 0.3;
}

.social__item:not(:nth-of-type(3n)) {
  margin-right: 20px;
}

